import * as React from 'react';
import {
  SCROLL_BAR_HEIGHT,
  MOBILE_SPACING,
  SPACING,
} from '../../utils/constants';

interface Props {
  children: React.ReactElement;
  visible?: boolean;
  isMobile: boolean;
}

const TableWrapper = (props: Props) => {
  const { children, visible, isMobile, ...rest } = props;

  const style: React.CSSProperties = {
    paddingTop: isMobile ? MOBILE_SPACING : SPACING,
    paddingBottom: (isMobile ? MOBILE_SPACING : SPACING) - SCROLL_BAR_HEIGHT,
  };

  if (visible === false) {
    style.display = 'none';
  }

  return (
    <div {...rest} style={style}>
      {children}
    </div>
  );
};

export default TableWrapper;
